.course-card {
  color: #424242;
  background: #26a69a;
  border: 3px solid #00897b;
  float: left;
  font-size: 25px;
  font-weight: bold;
  line-height: 100px;
  height: 100px;
  width: 200px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 30px;
  text-align: center;
}

.course-card:focus {
  outline: none;
}

.kbd-navigation .course-card:focus {
  background: #ddd;
}

.container {
  /* display: flex;
  flex-direction: row; */
  justify-content: center;
}

.course-board {
  /* display: flex;
  flex-direction: column; */
  padding-left: 120px;
}

/* restart rules are from my board game, but they might come in handy for other buttons. clear, etc? -Geoff */

.restart-button {
  margin-top: 20px;
  text-align: center;
}

.restart:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.restart:focus {
  outline: none;
}

.restart:active {
  box-shadow: 0 1px #424242;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
}

.restart { /* Safari */
  transition-duration: 0.4s;
  background-color: #424242;
  color: #fffde7;
  border: none;
  font-size: 24px;
  line-height: 48px;
  height: 48px;
  width: 40%;
  text-align: center;
  border-radius: 4px;
}
.App {
  text-align: center;
  height: 100vh;
  margin: 2rem auto;
  display: grid;
  grid-template-areas: "header header header"
                        "left main right";
  grid-template-rows: 1fr 3fr;
  grid-template-columns: 0.5fr 2.5fr 1fr;
  background-color: #282c34;
  color: white;
  font-family: 'Poppins', sans-serif;
}

.cell-main {
  grid-area: main;
  background: green;
  border-radius:25px;
  border: 3px solid;
}

.cell-left {
  border-radius: 25px;
  border: 2px solid rgb(19, 255, 255);
  grid-area: left;
}

.cell-right {
  border-radius: 25px;
  border: 2px solid rgb(19, 255, 255);
  grid-area: right;
}

.App-header {
  grid-area: header;
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

